import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import Drawer from 'material-ui/Drawer';
import MenuItem from 'material-ui/MenuItem';
import './App.css';

class App extends Component {
  constructor( props ) {
    super( props );
    this.state = { open: false };
    this.handleToggle = this.handleToggle.bind( this )
  }

  handleToggle() {
    this.setState({ open: !this.state.open });
  }

  handleClose() {
    this.setState({ open: false });
  }

  render() {
    return (
      <MuiThemeProvider>
        <div>
          <AppBar
            title={'GoEat'}
            onLeftIconButtonTouchTap={this.handleToggle}
          />
          <Drawer
            docked={false}
            open={this.state.open}
            onRequestChange={( open ) => this.setState({ open })}>
            <MenuItem onClick={this.handleClose}>Calculate Calories</MenuItem>
            <MenuItem onClick={this.handleClose}>Checkout diet (Bulking)</MenuItem>
            <MenuItem onClick={this.handleClose}>Checkout diet (Cutting)</MenuItem>
            <MenuItem onClick={this.handleClose}>Expert Opinion</MenuItem>
            <MenuItem onClick={this.handleClose}>Peer views</MenuItem>
          </Drawer>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default App;



